<template>
  <v-container>
    <v-row align-content-md="center" justify="center" align="center">
      <v-col cols="auto">
        <v-card width="600" class="pa-5 mt-10">
          <v-card-title>
            <span class="font-weight-bold">Add Customer Here</span>
          </v-card-title>
          <v-card-text>
            <v-form @submit.prevent="addCustomer">
              <v-row>
                <v-col cols="12">
                  <v-text-field label="Date" type="date" v-model="date"></v-text-field>
                  <v-text-field label="Name" type="text" v-model="name"></v-text-field>
                  <v-text-field label="Reason" type="text" v-model="reason"></v-text-field>
                  <v-text-field label="Amount" type="number" v-model="amount"></v-text-field>
                  <v-select :items="items" v-model="type" label="Transaction Type"></v-select>
                  <v-row>
                    <v-col cols="auto">
                      <v-btn
                        class="primary mb-3"
                        @click="addTransaction"
                        :loading="isLoading"
                        large
                      >Add</v-btn>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-form>
          </v-card-text>
          <v-btn to="/allTransactions" class="primary">Go to All Transactions</v-btn>
        </v-card>
      </v-col>
    </v-row>
    <v-snackbar top right :color="color" timeout="3000" v-model="snackbar">{{ message }}</v-snackbar>
  </v-container>
</template>

<script>
import axios from "../../axios";

export default {
  data() {
    return {
      name: "",
      reason: "",
      date: "",
      snackbar: false,
      message: "",
      amount: "",
      color: "green",
      isLoading: false,
      type: "Cash In",
      items: ["Cash In", "Cash Out"]
    };
  },
  methods: {
    addTransaction() {
      this.isLoading = true;
      var data = {
        name: this.name,
        reason: this.reason,
        type: this.type,
        amount: this.amount,
        date: this.date
      };
      if (this.name && this.reason && this.type && this.amount && this.date) {
        axios()
          .post("/api/addTransaction", data)
          .then(response => {
            this.snackbar = true;
            this.message = "Added";
            this.isLoading = false;
          })
          .catch(error => {
            this.snackbar = true;
            this.message = "Failed to Add";
            this.color = "error";
            this.isLoading = false;
          });
      } else {
        this.snackbar = true;
        this.message = "All Fields are required";
        this.isLoading = false;
        this.color = "error";
      }
    }
  }
};
</script>