<template>
  <v-container fluid class="mx-2">
    <v-row justify="center" justify-lg="center" justify-sm="center" justify-md="center">
      <v-col cols="12">
        <v-btn class="primary mb-3" to="/addproduct">Add Product</v-btn>
        <v-progress-linear indeterminate :active="isLoading"></v-progress-linear>
        <v-simple-table :fixed-header="fixedHeader" v-if="products.length >0">
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">Date</th>
                <th class="text-left">Item</th>
                <th class="text-left">Truck No</th>
                <th class="text-left">From</th>
                <th class="text-left">To</th>
                <th class="text-left">Amount</th>
                <th class="text-left">Delete</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(product) in products" :key="product._id">
                <td>{{ product.date }}</td>
                <td>{{ product.item }}</td>
                <td>{{ product.truckNo }}</td>
                <td>{{ product.from }}</td>
                <td>{{ product.to }}</td>
                <td>{{ product.quantity * product.rate }}</td>
                <td class="text-center">
                  <v-icon color="red" @click="deleteproduct(product._id )">delete</v-icon>
                </td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        <div v-else>
          <p class="text-center text-5">No Data found</p>
          <p class="text-center">
            Please add some products.
            <router-link to="/addProduct">Click here</router-link>
          </p>
        </div>
      </v-col>
    </v-row>
    <v-snackbar top right :color="color" timeout="3000" v-model="snackbar">{{ message }}</v-snackbar>
  </v-container>
</template>

<script>
import axios from "../../axios";

export default {
  data() {
    return {
      isLoading: true,
      color: "green",
      fixedHeader: true,
      snackbar: false,
      message: "",
      products: [],
      productsReceived: 0,
      productsSend: 0
    };
  },
  created() {
    this.isLoading = true;
    axios()
      .get("/api/allProducts")
      .then(response => {
        this.isLoading = false;
        this.products = response.data;
      })
      .catch(err => {
        this.snackbar = true;
        this.message = "Cannot retrieve data at this time";
        this.isLoading = false;
        this.color = "error";
      });
  },
  methods: {
    deleteproduct(id) {
      this.isLoading = true;
      setTimeout(() => {
        this.isLoading = false;
        this.snackbar = true;
        this.color = "error";
        this.message = "Something went wrong. Please try again later";
      }, 10000);
      axios()
        .get(`/api/deleteProduct/${id}`)
        .then(response => {
          axios()
            .get("/api/allProducts")
            .then(response => {
              this.isLoading = false;
              this.snackbar = true;
              this.products = response.data;
              this.message = "Deleted Successfully";
              this.color = "success";
            })
            .catch(err => {
              console.log(err);
              this.snackbar = true;
              this.message = "Cannot Delete";
              this.isLoading = false;
              this.color = "error";
            });
        })
        .catch(error => {
          this.snackbar = true;
          this.message = "Unable to Delete";
          this.color = "error";
          this.isLoading = false;
        });
    }
  }
};
</script>