<template>
  <v-container fluid class="mx-2">
    <v-row justify="center" justify-lg="center" justify-sm="center" justify-md="center">
      <v-col cols="12">
        <v-btn class="primary mb-3" to="/addTransaction">Add Transaction</v-btn>
        <v-progress-linear indeterminate :active="isLoading"></v-progress-linear>
        <v-simple-table :fixed-header="fixedHeader" v-if="transactions.length >0">
          <template v-slot:default>
            <thead>
              <tr>
                <th class="text-left">No</th>
                <th class="text-left">Date</th>
                <th class="text-left">Name</th>
                <th class="text-left">Reason</th>
                <th class="text-left">Amount</th>
                <th class="text-left">Delete</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(transaction, index) in transactions" :key="transaction._id">
                <td>{{ index+1 }}</td>
                <td>{{ transaction.date }}</td>
                <td>{{ transaction.name }}</td>
                <td>{{ transaction.reason }}</td>
                <td v-if="transaction.type == 'Cash In'" class="green--text">
                  <v-icon color="green">arrow_downward</v-icon>
                  Cr. ₹{{ transaction.amount }}/-
                </td>
                <td v-else class="red--text">
                  <v-icon color="red">arrow_upward</v-icon>
                  Dr. ₹{{ transaction.amount }}/-
                </td>
                <td class="text-center">
                  <v-icon color="red" @click="deleteTransaction(transaction._id )">delete</v-icon>
                </td>
              </tr>
              <tr>
                <td></td>
                <td></td>
                <td></td>
                <td class="font-weight-bold">Received Amount</td>
                <td class="green--text">
                  <v-icon color="green">arrow_downward</v-icon>
                  Cr. ₹{{ transactionsReceived }}/-
                </td>
                <td></td>
              </tr>
              <tr>
                <td></td>
                <td></td>
                <td></td>
                <td class="font-weight-bold">Sent Amount</td>
                <td class="red--text">
                  <v-icon color="red">arrow_upward</v-icon>
                  Dr. ₹{{ transactionsSend }}/-
                </td>
                <td></td>
              </tr>
            </tbody>
          </template>
        </v-simple-table>
        <div v-else>
          <p class="text-center text-5">No Data found</p>
          <p class="text-center">
            Please add some transactions.
            <router-link to="/addTransaction">Click here</router-link>
          </p>
        </div>
      </v-col>
    </v-row>
    <v-snackbar top right :color="color" timeout="3000" v-model="snackbar">{{ message }}</v-snackbar>
  </v-container>
</template>

<script>
import axios from "../../axios";

export default {
  data() {
    return {
      isLoading: true,
      color: "green",
      fixedHeader: true,
      snackbar: false,
      message: "",
      transactions: [],
      transactionsReceived: 0,
      transactionsSend: 0
    };
  },
  created() {
    this.isLoading = true;
    axios()
      .get("/api/allTransactions")
      .then(response => {
        this.isLoading = false;
        this.transactions = response.data;
        var data = response.data;
        for (var i = 0; i < data.length; i++) {
          if (data[i].type == "Cash In") {
            this.transactionsReceived += parseInt(data[i].amount);
          } else {
            this.transactionsSend += parseInt(data[i].amount);
          }
        }
      })
      .catch(err => {
        this.snackbar = true;
        this.message = "Cannot retrieve data at this time";
        this.isLoading = false;
        this.color = "error";
      });
  },
  methods: {
    deleteTransaction(id) {
      this.isLoading = true;
      setTimeout(() => {
        this.isLoading = false;
        this.snackbar = true;
        this.color = "error";
        this.message = "Something went wrong. Please try again later";
      }, 10000);
      axios()
        .get(`/api/delete/${id}`)
        .then(response => {
          this.transactionsSend = 0
          this.transactionsReceived = 0
          axios()
            .get("/api/allTransactions")
            .then(response => {
              this.isLoading = false;
              this.snackbar = true;
              this.transactions = response.data;
              var data = response.data;
              for (var i = 0; i < data.length; i++) {
                if (data[i].type == "Cash In") {
                  this.transactionsReceived += parseInt(data[i].amount);
                } else {
                  this.transactionsSend += parseInt(data[i].amount);
                }
              }
              this.message = "Deleted Successfully";
              this.color = "success";
            })
            .catch(err => {
              console.log(err);
              this.snackbar = true;
              this.message = "Cannot Delete";
              this.isLoading = false;
              this.color = "error";
            });
        })
        .catch(error => {
          this.snackbar = true;
          this.message = "Unable to Delete";
          this.color = "error";
          this.isLoading = false;
        });
    }
  }
};
</script>