<template>
  <v-container>
    <v-row align-content-md="center" justify="center" align="center">
      <v-col cols="auto">
        <v-card width="600" class="pa-5 mt-10">
          <v-card-title>
            <span class="font-weight-bold">Add Product Here</span>
          </v-card-title>
          <v-card-text>
            <v-form @submit.prevent="addProduct">
              <v-row>
                <v-col cols="12">
                  <v-text-field label="Date" type="date" v-model="date"></v-text-field>
                  <v-text-field label="Item" type="text" v-model="item"></v-text-field>
                  <v-text-field label="Truck No" @keyup="uppercase" type="text" v-model="truckNo"></v-text-field>
                  <v-text-field label="From"  type="text" v-model="from"></v-text-field>
                  <v-text-field label="To" type="text" v-model="to"></v-text-field>
                  <v-text-field label="Quantity" type="number" v-model="quantity"></v-text-field>
                  <v-text-field label="Rate" type="number" v-model="rate"></v-text-field>
                  <v-row>
                    <v-col cols="auto">
                      <v-btn class="primary mb-3" @click="addProduct" :loading="isLoading" large>Add</v-btn>
                    </v-col>
                  </v-row>
                </v-col>
              </v-row>
            </v-form>
          </v-card-text>
          <v-btn to="/allProducts" class="primary">Go to All Bricks Transactions</v-btn>
        </v-card>
      </v-col>
    </v-row>
    <v-snackbar top right :color="color" timeout="3000" v-model="snackbar">{{ message }}</v-snackbar>
  </v-container>
</template>

<script>
import axios from "../../axios";

export default {
  data() {
    return {
      item: "",
      truckNo: "",
      date: "",
      from: "",
      to: "",
      quantity: "",
      rate: "",
      snackbar: false,
      message: "",
      color: "green",
      isLoading: false
    };
  },
  methods: {
    addProduct() {
      this.isLoading = true;
      var data = {
        item: this.item,
        truckNo: this.truckNo,
        date: this.date,
        from: this.from,
        to: this.to,
        quantity: this.quantity,
        rate: this.rate
      };
      if (
        this.item &&
        this.truckNo &&
        this.date &&
        this.quantity &&
        this.rate &&
        this.to
      ) {
        axios()
          .post("/api/addProduct", data)
          .then(response => {
            this.snackbar = true;
            this.color = 'success';
            this.message = "Added";
            this.isLoading = false;
          })
          .catch(error => {
            this.snackbar = true;
            this.message = "Failed to Add";
            this.color = "error";
            this.isLoading = false;
          });
      } else {
        this.snackbar = true;
        this.message = "All Fields are required except From field";
        this.isLoading = false;
        this.color = "error";
      }
    },
    uppercase() {
      this.truckNo = this.truckNo.toUpperCase();
    }
  }
};
</script>